
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}



body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

p {
  font-size: 1.6rem;
}

h1 {
  font-size: 5rem;
}

.App {
  width: 100%;
  height: 100vh;
  position: relative;
  background-color: rgba(0,0,0,0.4);
   color: #fff;
}
.App:before {
  content: '';
  background: url('./asset/1000_F_342012883_55yxWeuNXbHiXYjayt8Xktii8wDoFyXA.jpg') no-repeat center center/cover;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: -1;
}

.search {
  text-align: center;
  padding: 1rem;
}

 input {
  padding: .7rem 1.5rem;
  font-size: 1.2rem;
  border-radius: 25px;
  border: 1px solid rgba(253, 253, 253, 0.7);
  background: rgba(14, 13, 13, 0.2);
  color: #f8f8f8;
}

.container {
  max-width: 500px;
  height: 580px;
  margin: auto;
  padding: 0 1rem;
  position: relative;
  top: 10%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

::placeholder{
  color: #f8f8f8;
}


.top {
  width: 100%;
  margin: 1rem auto;
}


.bottom {
  display: flex;
  justify-content: space-evenly;
  text-align: center;
  width: 100%;
  margin: rem auto;
  padding: 1rem;
  border-radius: 12px;
  background-color: rgb(119, 170, 230);
}

.bold {
  font-weight: 700;
}